<template>
	<div>
		<Breadcrumb class="bread">
			<div class="title">我的期权奖励</div>
			<!--<Button type="primary" size="large" icon="md-barcode" @click="timeList">时间表</Button>-->
		</Breadcrumb>
		<div>
			<Row>
				<Col span="24">
					<Card :bordered="false" class="capTable">
						<p slot="title" class="tipMessage">期权奖励列表</p>
						<div class="tabBoard">
							<Table height="700" :loading="loading" :border="true" :stripe="true" :columns="planTabList.rows" :data="planTabList.cols">
								<template slot-scope="{row , index}" slot="name">
									{{row.name}}
									<Icon type="ios-help-circle-outline" class="help" size="22" @click="showHelp"/>
								</template>
						        <template slot-scope="{ row, index }" slot="types">
						            <Button class="tabBtn" v-if="row.types != undefined" type="primary" size="small" style="margin-right: 5px" @click="isAddDrawer = !isAddDrawer">同意</Button>
						            <Button class="tabBtn" v-if="row.types != undefined" type="error" size="small" @click="cancelPlan">取消</Button>
						            <Button class="tabBtn" v-else type="primary" size="small" @click="timeList(index)">时间表</Button>
						        </template>
							</Table>
						</div>
					</Card>
				</Col>
			</Row>
		</div>
		 <Modal
			v-model="helpBox"
			title="期权机制描述"
			class-name="vertical-center-modal">
			<p>本公司计划于<b>【创建日期】</b>创建名为<b>【机制名称】</b>的期权激励计划（ESOP），该ESOP将预留共计<b>【股权量】</b>QT，约占公司股权的 * 作为期权池。</p>
			<p>该期权激励机制下，员工将会在第一个到期日被授予<b>【首次授权量】</b>的股份，其余75%的股份须于首个到期日期起按<b>【时间单位】</b> 授予，总计<b>【周期】</b> 个月。</p>
			
			<div slot="footer" class="tipMsg">
				<p>期权发行条款:</p>
				<p>期权严格按照既定计划发放。</p>
				<p>如果员工不再受雇于公司,任何未成熟期权将自动失效，并放回公司期权池。</p>
			</div>
		</Modal>
		<Drawer title="授权" width="400" :closable="false" v-model="isAddDrawer" class="newVoteBoard">
			<div>
				<h3>员工期权计划授权书</h3>
				<p>
					亲爱的爱丽丝,
				</p>
				<p>
					感谢您成为LittleBee Pte Ltd的员工。在本公司董事会的指示下，本公司已根据名为“普通员工期权计划”的公司期权计划向您授予100个公司股权代币，约为2%总股权。该期权的行使价格为1美元。不论公司未来的股份价值如何，期权行使价格应保持不变。
				</p>
				<p>
					你明白计划规则内所述的条款及条件规管该等股票期权，不论该等股票期权在本授权书日期前是否已获批。本规则将取代您与本公司之间关于期权的任何事先的理解或协议，无论是书面的还是其他形式的。
				</p>
				<p>
					期权行使后可转换为公司的可赎回股份。请注意，授予的选择权不是雇佣合同的一部分。如果您同意本公司按照本函所列条款及名为“普通员工期权计划”的期权方案，请用你的私钥签名，以表示您已接受本期权激励计划。
				</p>
				<p>2019年1月16日</p>
				<div class="btn">
					<Button type="success" long >签名</Button>
				</div>
			</div>
		</Drawer>
	</div>
</template>

<script>
	import Qs from 'qs'
	import {mapState} from 'vuex'
	import mutil from '@/util/mutil'
	export default {
		data(){
			return{
				helpBox:false,
				thisPlan:'1',
				isAddDrawer:false,
				loading:false,
				planList:[
					{
						value:'1',
						label:'期权计划1'
					},
					{
						value:'2',
						label:'期权计划2'
					},
					{
						value:'3',
						label:'期权计划3'
					},
					{
						value:'4',
						label:'期权计划4'
					}
				],
				planTabList:{
					rows:[
						{
							title: '机制名称',
							key: 'name',
							slot: 'name',
							align:'center'
						},
						{
							title: '期权授予量',
							key: 'allNum',
							align:'center'
						},
						{
							title: '授予占比',
							key: 'proportion',
							align:'center'
						},
						{
							title: '期权行使价格',
							key: 'getPay',
							align:'center'
						},
						{
							title: '授权日期',
							key: 'getDate',
							align:'center'
						},
						{
							title: '状态',
							key: 'types',
						 	slot: 'types',
							align:'center',
						}
					],
					cols:[
						{
							name:'期权计划1',
							allNum:100,
							proportion:50,
							getPay:30,
							getDate:20
						},
						{
							name:'期权计划2',
							allNum:100,
							proportion:50,
							getPay:30,
							getDate:20,
							types:'已同意'
						},
						{
							name:'期权计划3',
							allNum:100,
							proportion:50,
							getPay:30,
							getDate:20,
							types:'已同意'
						},
						{
							name:'期权计划4',
							allNum:100,
							proportion:50,
							getPay:30,
							getDate:20,
							types:'已同意'
						}
					]
				}
			}
		},
		computed: {
			...mapState({
				Address: state => state.web3.coinbase
			})
		},	
		methods:{
			timeList(){
				this.$router.push({
					path:'timePage',
					query:{
						"only":this.$route.query.only,
						"planId":'2'
					}
				})
			},
			showHelp(){
				this.helpBox = true
			},
			cancelPlan(){
				console.log('确定取消');
			}
			
		},
		created(){
			this.$emit('menuActiveName', "14-3")
			this.$emit('menuOpenNames', "14")
			this.loading = false	
		}
	}
</script>
<style>
	.warningProgress .ivu-progress-bg{
		background-color: #ed4014
	}
	.tooltip-msg{
		width: 300px	
	}
	.ivu-tooltip{width: 100%;}
</style>
<style scoped lang="stylus">
	flexs()
		display:flex
		align-items: center
		justify-content: space-between
		
	.bread
		flexs()
	
	.tipMsg
		text-align: left;
	
	.tabBoard
		.help
			position: relative
			cursor:pointer
			top: -2px
			color: #2d8cf0	
		.tabBtn
			padding: 5px 15px
		
	.newVoteBoard
		h3,h4
			text-align: center
		
		h3
			font-size:16px
			margin-bottom: 30px
			
		p
			line-height:22px
			margin-bottom: 15px
			font-size:14px
		.btn
			margin-top: 50px
			span
				font-size: 14px	
</style>




